<template>
  <div class="box-controller">
       <button class="control btn-new"
        @click="gameStart"
       ><i class="ion-ios-plus-outline"></i>New game</button>

       <button class="control btn-roll" v-on:click="handleRollDice">
         <i class="ion-ios-loop"></i>Roll dice</button>

       <button class="control btn-hold" 
              v-on:click="handleHold"
              :class="{winanble:winable}">
       <i class="ion-ios-download-outline"></i>Hold</button> 

       <input type="number" 
        v-bind:value = "winScore"
        placeholder="Final score" class="final-score">    
  </div>
</template>

<script>
export default {
    
    name: 'controllers', 

    props:{
        winScore: [Number, String],
        winable:Boolean
    },

    methods:{

        gameStart: function(){
           console.log('start-controller');
           this.$emit('newGame');
        },

        handleRollDice: function(){
            this.$emit("handleRollDice");
        }, 

        handleHold: function(){
           this.$emit('handleHold')
        }, 

       /*  changScore(event){
            let data = event.target.value;
            this.$emit('changScore', data)
        }
 */

    }
}
</script>

<style>


/**********************************************
*** Control
**********************************************/
.control {
    position: absolute;
    width: 200px;
    left: 50%;
    transform: translateX(-50%);
    color: #555;
    background: none;
    border: none;
    font-family: Lato;
    font-size: 20px;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: 300;
    transition: background-color 0.3s, color 0.3s;
}
.control.disable {
    pointer-events: none;
}

.control:hover { font-weight: 600; }
.control:hover i { margin-right: 20px; }

.control:focus {
    outline: none;
}

.control i {
    color: #42b983;
    display: inline-block;
    margin-right: 15px;
    font-size: 32px;
    line-height: 1;
    vertical-align: text-top;
    margin-top: -4px;
    transition: margin 0.3s;
}

.btn-new { top: 45px;}
.btn-roll { top: 403px;}
.btn-hold { top: 467px;}

.final-score {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 520px;
    color: #555;
    font-size: 18px;
    font-family: 'Lato';
    text-align: center;
    padding: 10px;
    width: 160px;
    text-transform: uppercase;
}

.winanble{
    animation: winnerable 5s infinite;
 }

@keyframes winnerable {
  0% {background-color: yellow;}
  50% {background-color: red;}
  100% {background-color: yellow;}
}

.final-score:focus { outline: none; }
</style>


