<template>
  <div id="app">
    <div class="wrapper clearfix">
      <players
        :isWin="isWin"
        :activePlayer="activePlayer"
        :scores="scores"
        :currentScore="currentScore"
      />

      <controllers
        :winScore="winScore"
        v-bind:winable="winable"
        @newGame="newGame"
        @handleRollDice="handleRollDice"
        @handleHold="handleHold"
        @changScore="changScore"
      />

      <dices v-bind:dices="dices" />
      <popup-game v-bind:popupActive="popupActive"
            @handleGameStart="handleGameStart"
      />

    </div>
  </div>
</template>

<script>
import Dices from "./components/Dices";
import Players from "./components/Players";
import Controllers from "./components/Controllers";
import PopupGame from "./components/PopupGame";

export default {
  name: "app",

  data: function () {
    return {
      msg: "Welcome to Your Vue.js App",
      isPlaying: false,
      activePlayer: -1,
      scores: [0, 0],
      dices: [1, 1],
      currentScore: 0,
      winScore: 50,
      popupActive:false,
    };
  },

  methods: {
    handleGameStart(){
        this.popupActive = false;
    },

    newGame: function () {
      /* console.log('newGame2') */
      this.popupActive = true;
      this.isPlaying = true;
      this.activePlayer = (this.activePlayer==0) ? 1 : 0;      
      this.scores = [0, 0];
      this.dices = [1, 1];
      this.currentScore = 0;
      this.winScore = 50;
    },


    nextPlayer(){
        this.activePlayer = this.activePlayer == 0 ? 1 : 0;
        this.currentScore = 0;
    },
  
    handleRollDice: function () {
      if (!this.isPlaying) {
        alert("Please click NewGame");
        return;
      }
      let {activePlayer} = this;
      let newDices = [... this.dices]
        newDices[0] = this.diceRandom();
        newDices[1] = this.diceRandom();

      this.dices = newDices;
      /* next Player */
      if (newDices[0] == 1 || newDices[1] == 1) {
         this.nextPlayer();
      } else {
        this.currentScore += newDices[0] + newDices[1];
      }
    },

    handleHold: function () {
      if (this.currentScore>0){
        let {activePlayer, scores, currentScore}  = this;
        let newScores = [... scores];

        newScores[activePlayer] += currentScore;
        this.scores = newScores;      
    
        if (this.scores[activePlayer] >= this.winScore) {
            this.isPlaying = false;
    
        } else {
            this.nextPlayer()
        }
      }
    },

    changScore(data){
      this.winScore = parseInt(data)
    },

      diceRandom(){
        return Math.ceil(Math.random() * 6);
    },
    
  },

  computed: {
     winable(){
        return  (this.scores[this.activePlayer] + this.currentScore >= this.winScore);
     },

     isWin(){
       let {activePlayer, winScore, scores, currentScore} = this;
       return (scores[activePlayer] + currentScore >= winScore);
     },

   
  },

  components: {
    Dices,
    Players,
    Controllers,
    PopupGame,
  },
};
</script>

<style>
/**********************************************
*** GENERAL
**********************************************/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

body {
  background-image: linear-gradient(
      rgba(62, 20, 20, 0.4),
      rgba(62, 20, 20, 0.4)
    ),
    url(/public/assets/images/back.jpg);
  background-size: cover;
  background-position: center;
  font-family: Lato;
  font-weight: 300;
  position: relative;
  height: 100vh;
  color: #555;
}

.wrapper {
  width: 1000px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}
</style>



